.teams {
    height: max-content;
}

.teams__header {
    height: auto;
    background: linear-gradient(104.38deg, #28C9B6 0%, #1F9E8F 100%);
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    padding-top: 6%;
    padding-bottom: 2%;
}

.teams__header span {
    line-height: 1.1;
}

.team__content {
    background-color: white;
}

.team__content a:hover {
    text-decoration: underline;
    text-decoration-style: wavy;
    text-decoration-color: #EC3750;
    transition: 0.3s ease;
    text-underline-offset: 25%;

}

