.membercore_container{
    position: relative;
    text-align: center;
}
.memberCore {
    padding-top: 5rem;
}

.membercore_container_text{
    font-size: 7rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@media only screen and (max-width: 786px)
{
    .membercore_container_text {
        font-size: 4rem;
    }
}