.coc_container {
    position: relative;
    text-align: center;
}

.codeofconduct {
    padding-top: 5rem;
}

.coc_cont_text {
    font-size: 7rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.coc_content {
    padding: 7rem;
    color: black;
}

@media only screen and (max-width: 786px)
{
    .coc_content {
        padding: 2rem;
    }

    .coc_cont_text {
        font-size: 4rem;
        line-height: 1;
    }
}