.footer {
    background-color: #1f2d3d;
    position: relative;
    color: white;
}

.footer_navs {
    padding: 5% 2% 0 2%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: larger;
}

.logo {
    max-width: 8vw;
    position: absolute;
    left: 46%;
}

.footer_navs h3 {
    font-weight: 100;
    line-height: 2;
}

.footer_at {
    font-weight: 100;
    font-size: 1.17em;
}

.footer__social {
    font-size: 1.4em;
    display: flex;
    max-width: 10%;
    height: 17vh;
    margin: auto !important;
    padding: 2vh 0;
    justify-content: space-between;
    padding-bottom: 2%;
    color: inherit;
}

.footer__socialLeft {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.footer__socialRight {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.footer__socialCenter {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

@media only screen and (max-width: 786px)
{
    .logo {
        display: none;
    }

    .grid {
        max-width: 80%;
    }

    .footer__social {
        max-width: 30%;
    }
}