.navbar {
    overflow: hidden;
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: saturate(100%) blur(7px);
    position: relative;
    justify-content: space-between;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
}

.navbar__memberBtn {
    position: absolute;
    top: 20%;
    right: 1%;
}

.nav-comp {
    position: absolute;
    /* font-weight: 500; Weight not changing*/
    z-index: 1;
    top: 1.5rem;
    left: 18vw;
    margin: auto;
    width: 65%;
    font-size: 1.4rem;
    color: black;
    display: flex;
    justify-content: space-around;
}


.navbar a {
    text-decoration: none;
    color: black;
    cursor: pointer;
}

@media only screen and (max-width: 786px)
{
    .navbar__memberBtn {
        /* right: 20%; */
    }

    .nav-comp{ 
        display: none;
    }
}

@media only screen and (max-width: 400px)
{
    .nav-comp {
        visibility: hidden;
    }

}