@import url("https://assets.hackclub.com/fonts/Phantom_Sans_0.7/Bold.woff");

.upcoming {
  height: 100vh;
  font-family: "Phantom Sans";
  color: #ffffff;
  overflow: hidden;
  box-sizing: content-box;
  background-color: #33d6a6;
  -webkit-transition: background-color 1000ms linear;
  -ms-transition: background-color 1000ms linear;
  transition: background-color 1000ms linear;
}

@media only screen and (max-width: 768px) {
  .upcoming {
    height: 95vh;
  }
}

.upcoming__content {
  margin-top: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.upcoming__content h2 {
  font-size: 2.5rem;
  margin-top: 3%;
}

.upcoming__message {
  margin-top: 3%;
}

.upcoming__comingSoon {
  margin-top: 7%;
  padding-bottom: 7.4%;
}